.sidebar {
  background-color: aquamarine;
  width: 21vw;
  height: 100vh;
  position: fixed;
  z-index: 1;
  display: flex;
  flex-direction: column;
  // justify-content: center;
  justify-content: end;
  .content {
    background-color: burlywood;
    width: 20vw;
    height: 80%;
    border-radius: 10px;
    margin-top: 1vh;
    margin-left: 0.5vw;
  }
  .social-links {
    background-color: burlywood;
    width: 20vw;
    margin-top: 1vh;
    margin-left: 0.5vw;
    border-radius: 10px;
    height: 7%;
    margin-bottom: 10px;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
  }
}
