.sidebar {
  background-color: aquamarine;
  width: 21vw;
  height: 100vh;
  position: fixed;
  z-index: 1;
  display: flex;
  flex-direction: column;
  justify-content: end;
}
.sidebar .content {
  background-color: burlywood;
  width: 20vw;
  height: 80%;
  border-radius: 10px;
  margin-top: 1vh;
  margin-left: 0.5vw;
}
.sidebar .social-links {
